import { Component, React } from "react";
import './calculadora.css'


class Calculadora extends Component{
    handleCLick(){
        var alt = document.getElementById('alt').value;
        var peso = document.getElementById('peso').value;
        var imc = peso/(alt * alt);

        if(alt === '' || peso === ''){
            alert('Preencha todos os campos para calcular!')
        }
        else{

        if(imc < 18.5){
            document.getElementById('resTxt').innerText = "Seu imc é de: " + Math.round(imc) + " | Abaixo do peso";
        }
        else if(imc >= 18.5 && imc < 24.9){
            document.getElementById('resTxt').innerText = "Seu imc é de: " + Math.round(imc) + " | Peso normal";
        }
        else if(imc >= 25 && imc < 29.9){
            document.getElementById('resTxt').innerText = "Seu imc é de: " + Math.round(imc) + " | Sobrepeso";
        }
        else if(imc >= 30 && imc < 34.9){
            document.getElementById('resTxt').innerText = "Seu imc é de: " + Math.round(imc) + " | Obesidade Grau I";
        }
        else if(imc >= 35 && imc < 39.9){
            document.getElementById('resTxt').innerText = "Seu imc é de: " + Math.round(imc) + " | Obesidade Grau II";
        }
        else if(imc >= 40){
            document.getElementById('resTxt').innerText = "Seu imc é de: " + Math.round(imc) + " | Obesidade Grau III";
        }
    }
        
        
    }
    render(){
        return(
            <div className='main'>
                <h4>Insira sua altura (0.00m): </h4>
                <input className="input" id='alt'></input>
                <h4>Insira seu peso (00kG): </h4>
                <input className="input" id='peso'></input>
                <button className="btn" onClick={() => this.handleCLick()}>Calcular</button>
                {/*eslint-disable-next-line*/}
                <h3 id='resTxt' className='res'></h3>
            </div>
        )
    }
}

export default Calculadora